<template>
  <div class="container">
    <div
      v-watermark="{
        text: accountbase.userCode + getDate(),
        textColor: '#ccc',
      }"
      class="container-content"
    >
      <el-row type="flex">
        <el-col style="width: 123px; margin-right: 80px">
          <div class="avatar-container">
            <div class="avatar-container-header"></div>
          </div>
        </el-col>
        <el-col>
          <div class="text item">
            <span class="item-key"> {{ $t('accountMag.account') }}</span>
            <span class="item-value">{{ accountbase.userCode }}</span>
          </div>
          <div class="text item">
            <span class="item-key">{{ $t('accountMag.roleName') }}</span>
            <span class="item-value">{{ accountbase.roleName }}</span>
          </div>
          <div class="text item">
            <span class="item-key">{{ $t('accountMag.userName') }}</span>
            <span class="item-value">{{ accountbase.userName }}</span>
          </div>
          <!-- <div class="text item">
            <span class="item-key">{{ $t('accountMag.mobile') }}</span>
            <span class="item-value">{{ mobileStr }}</span>
            <a class="cor-blue" @click="changeMobile"> {{ $t('common.edit') }}</a>
          </div> -->
          <div class="text item">
            <span class="item-key">{{ $t('accountMag.phone') }}</span>
            <span class="item-value">{{ accountbase.phone }}</span>
            <a class="cor-blue" @click="changePhone"> {{ $t('common.edit') }}</a>
          </div>
          <div class="text item">
            <span class="item-key"> {{ $t('accountMag.email') }}</span>
            <span class="item-value">{{ accountbase.email }}</span>
          </div>
          <div class="text item">
            <span class="item-key">{{ $t('accountMag.fax') }}</span>
            <span class="item-value">{{ accountbase.fax }}</span>
            <a class="cor-blue" @click="changeFax"> {{ $t('common.edit') }}</a>
          </div>
          <div class="text item">
            <span class="item-key">{{ $t('accountMag.corp') }}</span>
            <span class="item-value">{{ accountbase.corpName }}</span>
          </div>
        </el-col>
      </el-row>
    </div>
    <AddEditDialog
      :visible.sync="addEditDialogVisible"
      :modify-type="modifyType"
      :user-id="userId"
      @modifyParam="getData()"
    ></AddEditDialog>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import AddEditDialog from './dialogs/AddEditDialog.vue'
import fetch from '@/request/fetch'
import moment from 'moment'
export default {
  components: {
    AddEditDialog,
  },
  data() {
    return {
      accountbase: {
        userCode: '-',
        roleName: '-',
        name: '-',
        mobile: '-',
        phone: '-',
        email: '-',
        fax: '-',
        cust: '-',
      },
      addEditDialogVisible: false,
      modifyType: '',
    }
  },
  computed: {
    ...mapGetters('user', ['userId']),
    mobileStr() {
      if (!this.accountbase.mobileDesc) {
        return this.accountbase.mobile
      }
      if (this.accountbase.areaCode && this.accountbase.mobile) {
        return this.accountbase.areaCode + '-' + this.accountbase.mobile
      }
      return '-'
    },
  },
  async mounted() {
    this.getData()
  },
  methods: {
    getDate() {
      return moment(new Date()).format('YYYY-MM-DD HH:mm:ss')
    },
    async getData() {
      const obj = await fetch(
        '/user/queryPersonDetail',
        {
          userId: this.userId,
          modifyFlag: 0,
        },
        { method: 'get' }
      )
      if (obj.emailDesc) {
        obj.email = obj.emailDesc
      }
      if (obj.faxDesc) {
        obj.fax = obj.faxDesc
      }
      if (obj.mobileDesc) {
        obj.mobile = obj.mobileDesc
      }
      if (obj.phoneDesc) {
        obj.phone = obj.phoneDesc
      }
      if (obj.userNameDesc) {
        obj.userName = obj.userNameDesc
      }
      if (obj.corpNameDesc) {
        obj.corpName = obj.corpNameDesc
      }
      this.accountbase = obj
    },
    async updateUser(updateForm) {
      await fetch(
        '/user/modifyPerson',
        { ...updateForm, userId: this.userId },
        { method: 'post' }
      )
      this.$message.success('修改成功')
      this.getData()
    },
    changePhone() {
      // this.$prompt('请输入办公号码', '提示', {
      //   confirmButtonText: '确定',
      //   cancelButtonText: '取消',
      //   // inputPattern: /^(\d{3,4}-)?\d{7,8}$/,
      //   // inputErrorMessage: '办公号码格式如:0376-1234567',
      // }).then(({ value }) => {
      //   this.updateUser({ phone: value })
      // })
      this.addEditDialogVisible = true
      this.modifyType = 'phone'
    },
    changeMobile() {
      // this.$prompt('请输入手机号码', '提示', {
      //   confirmButtonText: '确定',
      //   cancelButtonText: '取消',
      //   inputPattern: /^1\d{10}$/,
      //   inputErrorMessage: '手机号码不正确',
      // }).then(({ value }) => {
      //   this.updateUser({ mobile: value })
      // })

      this.addEditDialogVisible = true
      this.modifyType = 'mobile'
    },
    changeEmail() {
      this.$prompt('请输入邮箱', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern:
          /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        inputErrorMessage: '邮箱格式不正确',
      }).then(({ value }) => {
        this.updateUser({ email: value })
      })
    },
    changeFax() {
      // this.$prompt('请输入传真号码', '提示', {
      //   confirmButtonText: '确定',
      //   cancelButtonText: '取消',
      //   // inputPattern: /^(\d{3,4}-)?\d{7,8}$/,
      //   // inputErrorMessage: '传真格式如:0376-1234567',
      // }).then(({ value }) => {
      //   this.updateUser({ fax: value })
      // })
      this.addEditDialogVisible = true
      this.modifyType = 'fax'
    },
  },
}
</script>
