<template>
  <div>
    <el-dialog
      :title="modifyTitle"
      :visible.sync="v"
      :before-close="handleClose"
      width="520px"
      :close-on-click-modal="false"
      append-to-body
    >
      <el-form
        ref="paramFormRef"
        :model="paramForm"
        :rules="paramRules"
        size="small"
        class="demo-ruleForm"
      >
        <el-form-item
          v-if="modifyType === 'mobile'"
          :label="$t('accountMag.mobile')"
          prop="mobile"
        >
          <el-input
            v-model="paramForm.mobile"
            :placeholder="inputPh('accountMag.mobile')"
          >
            <ct-select
              slot="prepend"
              v-model="paramForm.areaCode"
              :placeholder="$t('accountMag.areaCode')"
              :options="areaCodeOptions"
              filterable
              style="width: 80px"
            >
            </ct-select>
          </el-input>
        </el-form-item>
        <el-form-item
          v-if="modifyType === 'phone'"
          :label="$t('accountMag.phone')"
          prop="phone"
        >
          <el-input
            v-model="paramForm.phone"
            :placeholder="inputPh('accountMag.phone')"
          ></el-input>
        </el-form-item>
        <el-form-item
          v-if="modifyType === 'fax'"
          :label="$t('accountMag.fax')"
          prop="fax"
        >
          <el-input
            v-model="paramForm.fax"
            :placeholder="inputPh('accountMag.fax')"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button type="primary" size="small" @click="clickOk">{{
          $t('common.ok')
        }}</el-button>
        <el-button size="small" @click="cancel">{{ $t('common.cancel') }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import langMixin from '@/mixins/langMixin'
import fetch from '@/request/fetch'
export default {
  mixins: [langMixin],
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    modifyType: {
      type: String,
      default: '',
    },
    userId: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      paramForm: {},
      areaCodeOptions: [],
      paramRules: {
        mobile: [{ validator: this.validatePhone, trigger: 'blur' }],
      },
    }
  },
  computed: {
    v: {
      get: function () {
        return this.visible
      },
      set: function (value) {
        this.$emit('update:visible', value)
      },
    },
    modifyTitle() {
      if (this.modifyType === 'mobile') {
        return this.$t('common.edit') + this.$t('accountMag.mobile')
      }
      if (this.modifyType === 'phone') {
        return this.$t('common.edit') + this.$t('accountMag.phone')
      }
      if (this.modifyType === 'fax') {
        return this.$t('common.edit') + this.$t('accountMag.fax')
      }
      return ''
    },
  },
  watch: {
    visible(val) {
      if (val) {
        if (this.modifyType === 'mobile') {
          this.paramForm = {
            areaCode: '',
            mobile: '',
          }
        }
        if (this.modifyType === 'phone') {
          this.paramForm = {
            phone: '',
          }
        }
        if (this.modifyType === 'fax') {
          this.paramForm = {
            fax: '',
          }
        }
      }
    },
  },
  mounted() {
    this.queryAreaCodeList()
  },
  methods: {
    async queryAreaCodeList() {
      const res = await fetch('/user/areaCodeList', {}, { method: 'get' })
      this.areaCodeOptions = res.map((item) => {
        return {
          value: item,
          label: item,
        }
      })
    },
    clickOk() {
      this.$refs.paramFormRef.validate(async (valid) => {
        if (!valid) return

        await fetch(
          '/user/modifyPerson',
          { ...this.paramForm, userId: this.userId },
          { method: 'post' }
        )
        this.$refs.paramFormRef.clearValidate()
        this.$message.success(this.$t('common.editSuccess'))
        this.v = false
        this.$emit('modifyParam', false)
      })
    },
    cancel() {
      this.v = false
      this.$refs.paramFormRef.clearValidate()
    },
    handleClose() {
      this.cancel()
    },
    validatePhone(rule, value, callback) {
      if (!this.paramForm.areaCode) {
        callback(new Error(this.$t('accountMag.selectAreaCode')))
      }
      if (!/^\d{1,15}$/.test(value)) {
        callback(new Error(this.$t('accountMag.correctMobile')))
      }
      callback()
    },
  },
}
</script>
<style lang="scss" scoped></style>
